<template>
    <div class="book">
        <div class="search">
            <span @click="$router.back(-1)">关闭</span>
            <form>
                <input type="text" @focus="focus()" @blur="blur()"/>
                <van-icon name="search" size="0.16rem" color="#000000" v-if="flag"/>
            </form>
        </div>
        <div class="btnList">
            <li v-for="(v,i) in searchData" :key="i">
                <router-link :to="v.url">
                    <span :style="{color:v.color}">{{v.name}}</span>
                    <span>{{v.text}}</span>
                </router-link>
            </li>
        </div>
        <li>
            <router-link to="/login">注册账号</router-link>
            <span>|</span>
            <router-link to="/zhuce">登录豆瓣</router-link>
        </li>
        <li>
            <router-link to="/">使用桌面版</router-link>
            <span>|</span>
            <router-link to="/">使用豆瓣App</router-link>
        </li>
    </div>
</template>
<script>
export default {
    data() {
        return {
            flag:true,
            searchData:[]
        };
    },
    methods: {
        focus(){
            this.flag = false;
        },
        blur(){
            this.flag = true;
        }
    },
    mounted() {
        this.axios({
            url:"/api/search/search",
            method:"get"
        }).then(res=>{
            this.searchData = res.data.data;
        })
    },
}
</script>
<style scoped>
    li{
        list-style: none;
    }
    .search{
        box-sizing: border-box;
        padding: 0 0.15rem;
        width: 100%;
        height: 0.4rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 0.14rem;
        color: green;
        border-bottom: 1px solid #cccccc;
    }
    .search form{
        width: 3rem;
        height: 0.3rem;
        position: relative;
    }
    .search form input{
        padding-left: 0.15rem;
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        background-color: #d5d6d3;
        border: none;
        color: #000000;
    }
    .search form i{
        position: absolute;
        top: 0.07rem;
        left: 1.34rem;
    }
    .btnList{
        margin: 0.2rem auto 0;
        width: 3rem;
        display: flex;
        font-size: 0.12rem;
        flex-wrap: wrap;
        justify-content: space-between;
        border-bottom: 1px solid #cccccc;
    }
    .btnList li{
        width: 1rem;
        display: flex;
        justify-content: center;
        margin-bottom: 0.2rem;
    }
    .btnList li a{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .btnList li a span:first-child{
        font-size: 0.2rem;
    }
    .btnList li a span:last-child{
        color: #333333;
    }
    .book>li{
        width: 3rem;
        border-bottom: 1px solid #cccccc;
        height: 0.4rem;
        font-size: 0.14rem;
        margin: 0 auto;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    .book>li a{
        width: 1rem;
        text-align: center;
        color: green;
    }
    .book>li span{
        color: #cccccc;
        font-size: 0.18rem;
    }
</style>